<template>
  <div class="auto" style="padding-right:5px;">
    <div class="panel panel-info">
    	<div class="panel-heading">
    		<h3 class="panel-title text-center"><span @click="showbasic()" style="cursor: pointer;" class="pull-left">用户基本信息</span></h3>
    	</div>
      <div  class="panel-body" v-if="showbasicinfo">
        <div class="panel-body">
            <span>客户名称：{{model.f_user_name}}</span>
            <span>电话：{{model.f_phone}}</span>
            <span>片区：{{model.f_area}}</span>
            <span>报建类型：{{model.f_apply_type}}</span>
            <span>用户类型：{{model.f_user_type}}</span>
            <span>申请户数：{{model.f_apply_count}}</span>
            <span>房屋类型：{{model.f_house_type}}</span>
            <span>小区：{{model.f_residential_area}}</span>
            <span>地址：{{model.f_address}}</span>
            <span>报建备注：{{model.f_apply_remarks}}</span>
        </div>
        <!--<div class="panel panel-body">-->
          <!--<span>工程名称：{{model.f_total_cost}}</span>-->
          <!--<span>地址：{{model.f_actualfee}}</span>-->
          <!--<span>合同号：{{model.f_contract_number}}</span>-->
          <!--<span>合同金额：{{$parent.$refs.prestorefee.sum}}</span>-->
        <!--</div>-->
        <div class="panel panel-body" v-if="$login.f.f_fengongsi.includes('博能')">
          <p>票据历史:</p>
          <div>
            <span>累计开票金额：{{model.f_total_cost}}</span>
          </div>
          <div v-for="tem in temp">
            <span>票据类型：{{model.f_total_cost}}</span>
            <span>金额：{{model.f_total_cost}}</span>
            <span>开票人：{{model.f_actualfee}}</span>
            <span>开票时间：{{aaa}}</span>
          </div>
        </div>
        <!--<div class="panel panel-body">-->
          <!--<span>总计金额：{{model.f_total_cost}}</span>-->
          <!--<span>实缴金额：{{model.f_actualfee}}</span>-->
          <!--<span>未交金额：{{aaa}}</span>-->
        <!--</div>-->
      </div>
      <div  class="panel-body" v-if="showallinfo">
        <allselect :applyinfo="model"></allselect>
      </div>
    </div>
</div>
</template>
<script>
  export default {
    title: '基本信息',
    data () {
      return {
        temp : 2,
        showbasicinfo: true,
        showallinfo: false,
        model: {
          f_user_name: '无',
          f_address: '无',
          f_apply_count: '无',
          f_phone: '无',
          f_apply_type: '无',
          f_area: '无',
          f_user_type: '无',
          f_house_type: '无',
          f_residential_area: '无',
          f_apply_remarks: '无'
        }
      }
    },
    props: ['infeeinfo'],
    ready () {
      this.model = Object.assign({}, this.model, this.infeeinfo)
      console.log('ttttttttttttttthis.model', this.model)
    },
    methods: {
      showbasic () {
        this.showbasicinfo = true
        this.showallinfo = false
      },
      showall () {
        this.showbasicinfo = false
        this.showallinfo = true
      }
    },
    watch: {
      'infeeinfo' () {
        this.model = Object.assign({}, this.model, this.infeeinfo)
      }
    },
    computed: {
      aaa () {
        this.model.f_jmhj_cost = Number(this.model.f_total_cost)-Number(this.model.f_actualfee)
        return this.model.f_jmhj_cost
      }
    }
  }
</script>
<style>
</style>



